import React from "react";

function Contact(){
    return(
        <>
          <div className ="container">
            <div className ="row">
              <div className ="col-10 mx-auto" >
                <form className ="pt-5">
                  <div className ="mb-3">
                    <label for="exampleFormControlInput1" className ="form-label text_strength">Full Name</label>
                    <input type="email" className ="form-control" id="exampleFormControlInput1" placeholder="Enter your name here" />
                  </div>
                  <div className ="mb-3">
                    <label for="exampleFormControlInput1" className ="form-label text_strength">Mobile Number</label>
                    <input type="email" className ="form-control" id="exampleFormControlInput1" placeholder="Enter your mobile number here" />
                  </div>
                  <div className ="mb-3">
                    <label for="exampleFormControlInput1" className ="form-label text_strength">Email Address</label>
                    <input type="email" className ="form-control" id="exampleFormControlInput1" placeholder="name@example.com" />
                  </div>
                  <div className ="mb-3">
                    <label for="exampleFormControlTextarea1" className ="form-label text_strength">Message</label>
                    <textarea className ="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                  </div>
                  <button class="btn btn_color" type="submit">Submit</button>
                </form>
              </div>
            </div>
          </div>
        </>
    )
}

export default Contact;