import React from "react";

function Common(props){
    return(
        <>
    <section className="header">
        <div className="container-fluid">
            <div className="row">
                <div className="col-10 mx-auto">
                    <div className="row">
                        <div className="col-lg-6 col-md-6 pt-5 order-2 text_style">
                            <div className="text_wrapper p-2 rounded mx-lg-5">
                                <h4 className="text-center"><u>{props.text}</u></h4>
                                <h5>Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                                    Aenean malesuada sit amet felis vitae tincidunt. 
                                    Maecenas nec tellus elit. Integer pulvinar in nibh id sollicitudin.
                                     Duis feugiat malesuada lectus, vitae ornare ligula consectetur sed. 
                                     Proin tristique nisi ultricies justo tristique, sed tincidunt orci lobortis.
                                      Cras ut vehicula ante. Vestibulum eleifend.
                                </h5>
                            </div>
                        </div>
                            <div className="col-lg-6 col-md-6 order-1 pt-5 d-flex justify-content-center align-items-center">
                                <img className=" rounded img_prop animated" src={props.img_src} alt="image1" />
                            </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
        </>
    )
}

export default Common;